.game-section-start__container {
  margin: 0 auto;
  width: 480px;
  height: 460px;
  background-color: #c2f5ff;
  border-radius: 12px;
}

.game-menu__title {
  margin: 0 auto 1.75rem;
  display: flex;
  justify-content: center;
  align-items: center;

  /* width: 208px; */
  height: 96px;
  text-align: center;
  font-weight: 400;
  line-height: 48px;
  color: #004980;
  padding-top: 3.25rem;
  font-size: 2.5rem;
}

.game-menu-btn {
  font-family: StratosSkyeng, sans-serif;
  cursor: pointer;
  margin-top: 3rem;
  margin-left: 2.75rem;
  background: #fff;
  border-radius: 12px;
  width: 98px;
  height: 98px;
  border: none;
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 72px;
  color: #0080c1;
}

.game-menu__start-btn {
  font-family: StratosSkyeng, sans-serif;
  cursor: pointer;
  margin: 4.2rem auto 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 246px;
  height: 50px;
  background: #7ac100;
  border-radius: 12px;
  border: none;
  font-weight: 400;
  font-size: 1.5rem;
  line-height: 32px;
  color: #fff;
}

.game-menu-btn_checked {
  transform: scale(1.1);
  outline-color: #0071a9;
}

.game-section-cards__container {
  width: 698px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.game-table__card {
  display: flex;
  position: relative;
}

.successfully {
  opacity: 0.3;
}

.card__face {
  display: flex;
  background: url("./cards/рубашка.svg");
  border-radius: 4px;
  width: 95px;
  height: 133px;
  position: relative;
  transition: 1s;
}

.card__top {
  font-family: StratosSkyeng, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 24px;
  position: absolute;
  left: 10%;
  top: 0%;
}

.card__back {
  z-index: 1;
  position: absolute;
  left: 0;
  top: 0;
  display: none;
  background: url("./cards/рубашка.svg");
  border-radius: 4px;
  width: 95px;
  height: 133px;
  transform: rotateX("180deg");
}

.card__suit {
  width: 7px;
}

.card__bottom {
  font-family: StratosSkyeng, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 24px;
  position: absolute;
  right: 10%;
  bottom: 5%;
}

.game_content {
  display: flex;
  width: 100%;
  flex-flow: row wrap;
  justify-content: space-between;
  margin-bottom: 40px;
  align-items: center;
}

.game_content_button {
  font-family: StratosSkyeng, sans-serif;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 246px;
  height: 50px;
  background: #7ac100;
  border-radius: 12px;
  border: none;
  font-weight: 400;
  font-size: 1.5rem;
  line-height: 32px;
  color: #fff;
}

.timer {
  font-family: StratosSkyeng, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 72px;
  color: #fff;
}

.timer__text {
  display: flex;
  justify-content: flex-end;
  gap: 40%;
  font-family: StratosSkyeng, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  color: #fff;
}

.popup {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 4;
}

.timer_result-img {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.game-menu_result-title {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-weight: 400;
  line-height: 48px;
  color: #004980;
  font-size: 2.5rem;
}

.timer_result {
  margin-top: 1rem;
  font-family: StratosSkyeng, sans-serif;
  display: flex;
  justify-content: center;
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 72px;
  color: #000;
}

.title-time {
  display: flex;
  justify-content: center;
  font-family: StratosSkyeng, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
  margin: 0 auto;
  align-items: center;
  margin-top: 2rem;
  color: #000;
}

.game-cards-box {
  width: 698px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  position: relative;
}

.card {
  width: 95px;
  height: 133px;
  background: #fff;
  border-radius: 4px;
  position: relative;
  cursor: pointer;
}

.card_value__up {
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  color: #000;
  position: absolute;
  top: 2px;
  left: 6px;
}

.card_value__down {
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  color: #000;
  position: absolute;
  bottom: 2px;
  right: 6px;
  rotate: 180deg;
}

.suit_big {
  position: absolute;
  top: 55px;
  left: 33px;
}

.suit_small__up {
  width: 7px;
  height: 6px;
  position: absolute;
  top: 30px;
  left: 9px;
}

.suit_small__down {
  width: 7px;
  height: 6px;
  position: absolute;
  bottom: 30px;
  right: 9px;
  rotate: 180deg;
}
